import React,{useState} from 'react'

export default function TextForms(props) {
  const handleUpClick = ()=>{
    console.log("uppercase was clicked");
    let text1=text.toUpperCase();
    //setText("You Have clicked on handleUpClick");
    setText(text1);
    props.showAlert("Converted To Uppercase","success");
  }

  const handleLoClick = ()=>{
    console.log("uppercase was clicked");
    let text1=text.toLowerCase();
    //setText("You Have clicked on handleUpClick");
    setText(text1);
    props.showAlert("Converted To Lowercase","success");
  }

  const handleOnChange = (event)=>{
    //console.log("uppercase was clicked");
    setText(event.target.value);
  }

  const repwor =()=>{
    let toreplaced=prompt("Enter The Word To Be Replaced : ");
    let tobereplaced=new RegExp(toreplaced,'g');
    let repval=prompt("Enter The replaced word : ");
    let newtext=text.replace(tobereplaced,repval);
    setText(newtext);
    props.showAlert("word successfully replaced","success");
  }

  const tocleartext = ()=>{
    setText('');
    props.showAlert("All text cleared ","success");
  }

  // const firstcharcap = ()=>{
  //   //let newtext=text.charAt(0).toUpperCase()+text.slice(1);
  //   let newtext=text.split(" ")
  //   newtext[0].toUpperCase();
  //   setText(newtext);
  // }

  const [text,setText]=useState('');
  //console.log(useState('Enter Text 2'))
  //text="new text";//wrong way to change the state 
  //setText("new text");


  const handlecopy = ()=>{
      var text=document.getElementById("exampleFormControlTextarea1");
      text.select();
      navigator.clipboard.writeText(text.value);
      document.getSelection().removeAllRanges();
      props.showAlert("Copied To Clipboard","success");
  }

  const handleextraspace = () =>{
    let newtext=text.split(/[ ]+/);
    setText(newtext.join(" "))
    props.showAlert("extra space removed","success");
  }

  return (
    <>
    <div className="container" style={{color : props.mode==='light'?'black' : 'white'}}>
    <h1>{props.heading}</h1>
      <div className="mb-3">
        <textarea
          className="form-control"
            value={text}  
              onChange={handleOnChange}
            id="exampleFormControlTextarea1"
            style={{backgroundColor : props.mode==='light'?'white' : '#0c32ad',color : props.mode==='light'?'black' : 'white',fontSize:'25px',cursorColor : 'white'} }
          rows="10"
        ></textarea>
      </div>


      <button disabled={text.length===0} className="btn btn-primary my-1" onClick={handleUpClick}>Convert To Uppercase</button>
      <button disabled={text.length===0} className="btn btn-primary mx-3 my-1" onClick={handleLoClick}>Convert To LowerCase</button>
      <button disabled={text.length===0} className="btn btn-primary my-1" onClick={repwor}>Replace All Occurences</button>
      <button disabled={text.length===0} className="btn btn-primary mx-3 my-1" onClick={tocleartext}>clear the console</button>
      {/* <button className="btn btn-primary mx" onClick={firstcharcap}>Capitalise all first character </button> */}
      <button disabled={text.length===0} className="btn btn-primary my-1" onClick={handlecopy}>copy text</button>
      <button disabled={text.length===0} className="btn btn-primary mx-3 my-1" onClick={handleextraspace}>remove extra space</button>


    </div>
     <div className="container my-5" style={{color : props.mode==='light'?'black' : 'white'}}>
        <h1 >Your Text Summary </h1>
        <p>{text.split(" ").filter((element)=>{return element.length!==0}).length} words || {text.length} characters || {text.split(".").filter((element)=>{return element.length!==0}).length} sentences. || {text.split("\n").filter((element)=>{return element.length!==0}).length
        } paragraphs ||</p>
        <p>{0.008*text.split(" ").filter((element)=>{return element.length!==0}).length} Minutes Read</p>
        <h2>Preview</h2>
        <p>{text.length>0?text:"Enter Something In The The Textbox Above To Preview Here"}</p>
     </div>
     
     
    </>
  );
}

TextForms.defaultProps = {
  heading: "This Is My Heading",
};
