// import logo from './logo.svg';
import './App.css';
import Navbar from './components/Navbar';
import TextForms from './components/TextForms';
import About from './components/About';
import Alert from './components/Alert';
import React from "react";
import { useState } from 'react';

import {
  BrowserRouter as Router,
  Route,
  // Link,
  Routes
} from "react-router-dom";

function App() {
  const [mode,setMode] = useState('light');//whether dark mode is ewnabled or not
  const [alert,setAlert]=useState(null);
  
  const showAlert=(message,type)=>{
      setAlert({
        msg: message,
        type: type,
      })

      setTimeout(() => {
        setAlert(null);
      }, 2000);
  }
  const togglemode =()=>{
    if(mode==='dark'){
      setMode('light');
      document.body.style.backgroundColor='white';
      showAlert("light mode has been enabled","primary");
    }
    else if(mode==='light'){
      setMode('dark');
      document.body.style.backgroundColor='#2d3063';
      showAlert("dark mode has been enabled","success");

    }
  }
  return (
    <>
    <Router>
    {/* <Navbar title="Textutils" hometext="Home"/> */}
    <Navbar  title="Text Analyzer" mode={mode} togglemode={togglemode}/>
    <Alert alert={alert}/>
    <div className="container my-3">
    <Routes>
          <Route exact path="/about" element={<About />}>
            {/* <About /> */}
          </Route>
          <Route exact path="/"element={<TextForms showAlert={showAlert} heading="Enter The Text To Analyze" mode={mode} />}>
            {/* <TextForms showAlert={showAlert} heading="Enter The Text To Analyze" mode={mode} /> */}
          </Route>
    </Routes>
    {/*  <About/> */}
    {/* <TextForms showAlert={showAlert} heading="Enter The Text To Analyze" mode={mode} /> */}
    </div>
    </Router>
    </>
  );
}

export default App;
